import React, { useEffect } from 'react';
import LogicFlow from '@logicflow/core';
import ExampleHeader from '../../../../components/example-header';
import '../../../../assets/iconfont/iconfont.css';
import './index.css';

const item = `<i class='iconfont icon-info'></i>`;

const config = {
  stopScrollGraph: true,
  stopZoomGraph: true,
  textEdit: false,
  tool: {
    menu: true,
    control: false,
  },
  nodeMenuConfig: [
    {
      text: item,
      className: 'lf-menu-item',
      callback(nodeModel: any) {
        console.log(nodeModel);
        alert(`
          id${nodeModel.id}
          ${nodeModel.type}
          (x: ${nodeModel.x}, y: ${nodeModel.y})`
        );
      },
    }
  ],
  edgeMenuConfig: [],
  graphMenuConfig: [
    {
      text: '',
      className: 'lf-menu-item',
      callback(graphModel: any) {
        alert('');
      },
    }
  ]
};

const data = {
  nodes: [
    {
      id: 10,
      type: 'rect',
      x: 150,
      y: 80,
      text: ''
    },
    {
      id: 20,
      type: 'rect',
      x: 400,
      y: 80,
      text: ''
    }
  ],
  edges: [
    {
      type: 'line',
      sourceNodeId: 10,
      targetNodeId: 20,
      text: '',
      endPoint: {
        id: '150-60',
        x: 350,
        y: 80
      }
    }
  ]
};

let lf: LogicFlow;

export default function CustomMenuExample() {

  useEffect(() => {
    lf = new LogicFlow({
      ...config,
      grid: {
        size: 10,
        type: 'dot'
      },
      container: document.querySelector('#graph') as HTMLElement
    });
    lf.render(data);
  }, []);

  return (
    <>
      <ExampleHeader content="" />
      <div id="graph" className="viewport" />
    </>
  )
}
